<template>
  <div id="MarketActivity">
    <div style="padding:10px;overflow:hidden;" >
        <div  v-for="(item,index) in Datalist" :key="index" @click="showdetail(index)">
            <div style="margin:10px auto;border-left: 5px red solid;background: #fff; padding: 10px 20px;">
                <div style="display:flex;">
                    <div style="background:yellow;padding:2px 5px;width: 30px;text-align: center;">{{item.type}}</div>
                    <div style="margin-left:10px;background:#999999;padding:2px 5px;width: 30px;text-align: center;" v-if="item.status==0">结束</div>
                </div>
                <div style="font-size:16px;margin:5px 0" >{{item.name}}</div>
                <div class="h30">time：{{item.time}}</div>
                <div class="h30">地址：{{item.address}}</div>
            </div>
            <div v-if="showindex==index" style="padding:0 20px" class=" wow bounceInUp">
                <div class="h30">time：{{item.time}}</div>
                <div class="h30">地址：{{item.address}}</div>
                <div class="h30">座位号：{{item.SeatNumber}}</div>
            </div>
        </div>
        <div></div>
    </div>
  </div>
</template>

<script>
export default {
name:'MarketActivity',
data() {
    return {
        showdetails: false,
        showindex:0,
      Datalist: [
          {type:'展会',status:0,name:'展会名称展会名称展会名称展会名称展会名称',time:'2021-22-21',address:'杭州',SeatNumber:'2-001'},
          {type:'展会',status:1,name:'展会名称展会名称展会名称展会名称展会名称',time:'2021-22-21',address:'杭州',SeatNumber:'2-001'},
          {type:'展会',status:1,name:'展会名称展会名称展会名称展会名称展会名称',time:'2021-22-21',address:'杭州',SeatNumber:'2-001'},
          {type:'展会',status:0,name:'展会名称展会名称展会名称展会名称展会名称',time:'2021-22-21',address:'杭州',SeatNumber:'2-001'}
      ]
    };
  },
  methods: {
    routerbank() {
      this.$router.go(-1);
    },
    showdetail(index){
        this.showindex=index
    }
  }
}
</script>

<style lang="scss" scoped >
.h30{
    line-height: 30px;font-size: 12px;color: #999999;
}
#MarketActivity{
    background: #eeeeee;
    min-height: 100vh;
    overflow: hidden;
}
.nav {
    background: #ffffff;
    position: fixed;
    width: 100%;
    top: 0;
    box-sizing: border-box;
    padding: 0 10px;
    display: flex;
    justify-content: space-between;
    line-height: 40px;
    .title {
      text-align: center;
    }
  }
</style>


